'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useWallet } from '@/contexts/WalletContext';
// // import {
//   "‚ûï",
//   "‚ûñ",
//   "üê¶",
//   "üì∑",
//   "üéµ",
// } from 'react-icons/fa'; // ‰∏¥Êó∂Ê≥®Èáä‰ª•‰øÆÂ§çÊûÑÂª∫ // ‰∏¥Êó∂Ê≥®Èáä‰ª•‰øÆÂ§çÊûÑÂª∫
import { toast } from 'react-hot-toast';

// ÂÆö‰πâ‰ªªÂä°Á±ªÂûã
type TaskType = 'exposure' | 'treasure' | 'lottery' | 'airdrop';
type SocialPlatform = 'twitter' | 'instagram' | 'tiktok';

export default function CreateTaskPage() {
  const router = useRouter();
  const { wallet } = useWallet();

  // ‰ªªÂä°Âü∫Êú¨‰ø°ÊÅØ
  const [taskType, setTaskType] = useState<TaskType>('exposure');
  const [taskTitle, setTaskTitle] = useState('');
  const [taskDescription, setTaskDescription] = useState('');
  const [rewardAmount, setRewardAmount] = useState(100);
  const [selectedPlatforms, setSelectedPlatforms] = useState<SocialPlatform[]>([
    'twitter',
  ]);
  const [durationDays, setDurationDays] = useState(7);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Ê®°Êãü‰ΩôÈ¢ùÊï∞ÊçÆ
  const balances = {
    sm: 1250.75,
    redFlower: 350,
  };

  // ËÆ°ÁÆó‰ªªÂä°ÊàêÊú¨
  const calculateCost = () => {
    let redFlowerCost = 0;

    // Âü∫‰∫é‰ªªÂä°Á±ªÂûãÂíåÂ•ñÂä±ËÆ°ÁÆóÊàêÊú¨
    switch (taskType) {
      case 'exposure':
        redFlowerCost = rewardAmount * 2;
        break;
      case 'treasure':
        redFlowerCost = rewardAmount * 3;
        break;
      case 'lottery':
        redFlowerCost = rewardAmount * 1.5;
        break;
      case 'airdrop':
        redFlowerCost = rewardAmount;
        break;
    }

    // Ê†πÊçÆÂπ≥Âè∞Â¢ûÂä†ÊàêÊú¨
    selectedPlatforms.forEach((platform) => {
      if (platform === 'twitter') redFlowerCost += 50;
      if (platform === 'instagram') redFlowerCost += 30;
      if (platform === 'tiktok') redFlowerCost += 40;
    });

    // Ê†πÊçÆÊó∂ÈïøÂ¢ûÂä†ÊàêÊú¨
    redFlowerCost += durationDays * 5;

    // ËÆ°ÁÆóÊúçÂä°Ë¥π
    const serviceFee = Math.ceil(redFlowerCost * 0.05);

    return {
      redFlower: Math.ceil(redFlowerCost),
      smFee: serviceFee,
    };
  };

  // Â§ÑÁêÜÂπ≥Âè∞ÈÄâÊã©
  const togglePlatform = (platform: SocialPlatform) => {
    if (selectedPlatforms.includes(platform)) {
      setSelectedPlatforms(selectedPlatforms.filter((p) => p !== platform));
    } else {
      setSelectedPlatforms([...selectedPlatforms, platform]);
    }
  };

  // Â¢ûÂä†/ÂáèÂ∞ëÂ•ñÂä±
  const adjustReward = (amount: number) => {
    const newAmount = rewardAmount + amount;
    if (newAmount >= 10 && newAmount <= 10000) {
      setRewardAmount(newAmount);
    }
  };

  // Êèê‰∫§Ë°®Âçï
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!wallet.isConnected) {
      toast.error('ËØ∑ÂÖàËøûÊé•Èí±ÂåÖ');
      return;
    }

    if (!taskTitle || !taskDescription || selectedPlatforms.length === 0) {
      toast.error('ËØ∑Â°´ÂÜôÂÆåÊï¥ÁöÑ‰ªªÂä°‰ø°ÊÅØ');
      return;
    }

    const cost = calculateCost();

    // Ê£ÄÊü•‰ΩôÈ¢ù
    if (balances.redFlower < cost.redFlower) {
      toast.error(`Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù‰∏çË∂≥ÔºåÈúÄË¶Å ${cost.redFlower} Êúµ`);
      return;
    }

    if (balances.sm < cost.smFee) {
      toast.error(`SM‰ª£Â∏Å‰ΩôÈ¢ù‰∏çË∂≥ÔºåÈúÄË¶Å ${cost.smFee} SM`);
      return;
    }

    setIsSubmitting(true);

    try {
      // Ê®°Êãü‰ªªÂä°ÂàõÂª∫ËøáÁ®ã
      await new Promise((resolve) => setTimeout(resolve, 1500));

      toast.success('‰ªªÂä°ÂàõÂª∫ÊàêÂäüÔºÅ');
      router.push('/tasks');
    } catch (error) {
      console.error('ÂàõÂª∫‰ªªÂä°Â§±Ë¥•:', error);
      toast.error('ÂàõÂª∫‰ªªÂä°Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
    } finally {
      setIsSubmitting(false);
    }
  };

  // ‰ªªÂä°Á±ªÂûãÈÄâÈ°π
  const taskTypes = [
    {
      id: 'exposure',
      name: 'ÊõùÂÖâ‰ªªÂä°',
      description: 'ËÆ©Áî®Êà∑Âú®Á§æ‰∫§Â™í‰Ωì‰∏äÂÆ£‰º†ÊÇ®ÁöÑÂÜÖÂÆπ',
    },
    {
      id: 'treasure',
      name: 'ËØùÈ¢òÂÆùÁÆ±',
      description: 'Áî®Êà∑ÂèÇ‰∏éÁâπÂÆöËØùÈ¢ò‰∫íÂä®ÂèØËé∑ÂæóÂ•ñÂä±',
    },
    { id: 'lottery', name: 'ÊäΩÂ•ñ‰ªªÂä°', description: 'Áî®Êà∑ÂÆåÊàêË¶ÅÊ±ÇÂêéÂèÇ‰∏éÊäΩÂ•ñ' },
    {
      id: 'airdrop',
      name: 'Á©∫Êäï‰ªªÂä°',
      description: 'Áî®Êà∑ÂÆåÊàêÁÆÄÂçï‰ªªÂä°Ëé∑ÂæóÂ•ñÂä±',
    },
  ];

  // ËÆ°ÁÆóÂΩìÂâçÊàêÊú¨
  const cost = calculateCost();

  return (
    <div className="max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold mb-8">ÂàõÂª∫Êñ∞‰ªªÂä°</h1>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* ‰ªªÂä°Á±ªÂûãÈÄâÊã© */}
        <div className="glass-card p-6">
          <h2 className="text-xl font-semibold mb-4">ÈÄâÊã©‰ªªÂä°Á±ªÂûã</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {taskTypes.map((type) => (
              <div
                key={type.id}
                onClick={() => setTaskType(type.id as TaskType)}
                className={`p-4 border rounded-lg cursor-pointer transition-colors ${
                  taskType === type.id
                    ? 'border-primary bg-primary/10'
                    : 'border-gray-700 hover:border-gray-500'
                }`}
              >
                <div className="font-semibold">{type.name}</div>
                <div className="text-sm text-gray-400 mt-1">
                  {type.description}
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* ‰ªªÂä°Âü∫Êú¨‰ø°ÊÅØ */}
        <div className="glass-card p-6">
          <h2 className="text-xl font-semibold mb-4">‰ªªÂä°‰ø°ÊÅØ</h2>

          <div className="space-y-4">
            <div>
              <label className="block text-sm text-gray-400 mb-1">
                ‰ªªÂä°Ê†áÈ¢ò
              </label>
              <input
                type="text"
                value={taskTitle}
                onChange={(e) => setTaskTitle(e.target.value)}
                className="w-full bg-gray-900 border border-gray-700 rounded-md px-3 py-2"
                placeholder="‰æãÂ¶ÇÔºöÂÖ≥Ê≥®Êàë‰ª¨ÁöÑTwitterË¥¶Âè∑Ëé∑ÂæóÂ•ñÂä±"
                maxLength={50}
                required
              />
            </div>

            <div>
              <label className="block text-sm text-gray-400 mb-1">
                ‰ªªÂä°ÊèèËø∞
              </label>
              <textarea
                value={taskDescription}
                onChange={(e) => setTaskDescription(e.target.value)}
                className="w-full bg-gray-900 border border-gray-700 rounded-md px-3 py-2 h-24"
                placeholder="ËØ¶ÁªÜÊèèËø∞ÊÇ®ÁöÑ‰ªªÂä°Ë¶ÅÊ±ÇÂíåÂ•ñÂä±..."
                maxLength={500}
                required
              />
            </div>

            <div>
              <label className="block text-sm text-gray-400 mb-1">
                Á§æ‰∫§Âπ≥Âè∞
              </label>
              <div className="flex flex-wrap gap-3">
                <button
                  type="button"
                  onClick={() => togglePlatform('twitter')}
                  className={`py-2 px-4 rounded-md flex items-center gap-2 ${
                    selectedPlatforms.includes('twitter')
                      ? 'bg-blue-600/70'
                      : 'bg-gray-800'
                  }`}
                >
                  üê¶
                  <span>Twitter</span>
                </button>
                <button
                  type="button"
                  onClick={() => togglePlatform('instagram')}
                  className={`py-2 px-4 rounded-md flex items-center gap-2 ${
                    selectedPlatforms.includes('instagram')
                      ? 'bg-purple-600/70'
                      : 'bg-gray-800'
                  }`}
                >
                  üì∑
                  <span>Instagram</span>
                </button>
                <button
                  type="button"
                  onClick={() => togglePlatform('tiktok')}
                  className={`py-2 px-4 rounded-md flex items-center gap-2 ${
                    selectedPlatforms.includes('tiktok')
                      ? 'bg-black border border-gray-400'
                      : 'bg-gray-800'
                  }`}
                >
                  üéµ
                  <span>TikTok</span>
                </button>
              </div>
            </div>

            <div>
              <label className="block text-sm text-gray-400 mb-1">
                ‰ªªÂä°Â•ñÂä± (Â∞èÁ∫¢Ëä±)
              </label>
              <div className="flex items-center">
                <button
                  type="button"
                  onClick={() => adjustReward(-10)}
                  className="p-2 bg-gray-800 rounded-l-md hover:bg-gray-700"
                >
                  ‚ûñ
                </button>
                <input
                  type="number"
                  value={rewardAmount}
                  onChange={(e) =>
                    setRewardAmount(parseInt(e.target.value) || 10)
                  }
                  className="w-24 text-center bg-gray-900 border-y border-gray-700 py-2"
                  min="10"
                  max="10000"
                />
                <button
                  type="button"
                  onClick={() => adjustReward(10)}
                  className="p-2 bg-gray-800 rounded-r-md hover:bg-gray-700"
                >
                  ‚ûï
                </button>
                <div className="ml-3 text-sm text-gray-400">Â∞èÁ∫¢Ëä±</div>
              </div>
            </div>

            <div>
              <label className="block text-sm text-gray-400 mb-1">
                ‰ªªÂä°Êó∂Èïø (Â§©)
              </label>
              <select
                value={durationDays}
                onChange={(e) => setDurationDays(parseInt(e.target.value))}
                className="bg-gray-900 border border-gray-700 rounded-md px-3 py-2"
              >
                <option value={3}>3Â§©</option>
                <option value={7}>7Â§©</option>
                <option value={14}>14Â§©</option>
                <option value={30}>30Â§©</option>
              </select>
            </div>
          </div>
        </div>

        {/* ÊàêÊú¨ÊëòË¶Å */}
        <div className="glass-card p-6">
          <h2 className="text-xl font-semibold mb-4">Ë¥πÁî®ÊëòË¶Å</h2>

          <div className="flex justify-between mb-2">
            <div>Â•ñÂä±Â∞èÁ∫¢Ëä±:</div>
            <div>{rewardAmount} Êúµ</div>
          </div>

          <div className="flex justify-between mb-2">
            <div>Âπ≥Âè∞Ë¥πÁî®:</div>
            <div>{cost.redFlower - rewardAmount} Êúµ</div>
          </div>

          <div className="flex justify-between mb-2">
            <div>ÊúçÂä°Ë¥π (SM‰ª£Â∏Å):</div>
            <div>{cost.smFee} SM</div>
          </div>

          <div className="border-t border-gray-700 mt-3 pt-3 flex justify-between font-semibold">
            <div>ÊÄªËÆ°:</div>
            <div>
              {cost.redFlower} ÊúµÂ∞èÁ∫¢Ëä± + {cost.smFee} SM
            </div>
          </div>

          <div className="flex justify-between mt-3 text-sm">
            <div>ÊÇ®ÁöÑ‰ΩôÈ¢ù:</div>
            <div>
              {balances.redFlower} ÊúµÂ∞èÁ∫¢Ëä± / {balances.sm} SM
            </div>
          </div>

          {(balances.redFlower < cost.redFlower ||
            balances.sm < cost.smFee) && (
            <div className="mt-3 text-red-400 text-sm">
              ‰ΩôÈ¢ù‰∏çË∂≥ÔºåÊó†Ê≥ïÂàõÂª∫‰ªªÂä°
            </div>
          )}
        </div>

        {/* Êèê‰∫§ÊåâÈíÆ */}
        <div className="flex justify-end">
          <button
            type="button"
            onClick={() => router.back()}
            className="mr-4 py-2 px-6 bg-gray-800 hover:bg-gray-700 rounded-md"
          >
            ÂèñÊ∂à
          </button>
          <button
            type="submit"
            disabled={
              isSubmitting ||
              balances.redFlower < cost.redFlower ||
              balances.sm < cost.smFee
            }
            className={`neon-button py-2 px-6 flex items-center ${
              isSubmitting ||
              balances.redFlower < cost.redFlower ||
              balances.sm < cost.smFee
                ? 'opacity-50 cursor-not-allowed'
                : ''
            }`}
          >
            {isSubmitting ? (
              <>
                <div className="w-4 h-4 border-2 border-t-transparent border-white rounded-full animate-spin mr-2"></div>
                Â§ÑÁêÜ‰∏≠...
              </>
            ) : (
              'ÂàõÂª∫‰ªªÂä°'
            )}
          </button>
        </div>
      </form>
    </div>
  );
}
